import { FC } from 'react'
import { v4 } from 'uuid'

/**
 * Simplified InputSimple component for Ziggy plugins
 */

export interface InputSimpleProps {
  className?: string
  value: string
  onChange: (value: string) => void
  width?: number
  placeholder?: string
  type?: string
  readonly?: boolean
}

const InputSimple: FC<InputSimpleProps> = ({
  className = '',
  value,
  width,
  onChange,
  placeholder = '',
  type = 'text',
  readonly = false,
}) => {
  const handleChange = (e: React.FormEvent<HTMLInputElement>) => {
    onChange(e.currentTarget.value)
  }

  return (
    <input
      id={v4()}
      name={v4()}
      style={{ width: width || '100%' }}
      className={`border-[1px] border-gray-300 rounded-md p-1 focus:ring-gray-400 ${className}`}
      autoComplete="new-password"
      placeholder={placeholder}
      onChange={handleChange}
      aria-label="input"
      value={value}
      type={type}
      readOnly={readonly}
    />
  )
}

export default InputSimple
